import React from 'react';

const ConfirmEmail = ({ email }) => {
  return (
    <div className="confirmEmailContent">
      <div className="confirmEmailTitle">
        Proszę potwierdź że podany email należy do Ciebie
      </div>
      <div className="confirmEmailBody">
        Na podany adres <strong>{email}</strong> wysłaliśmy prośbę o
        potwierdzenie że jesteś właścicielem tego e-mail. Jeśłi jej nie
        otrzymałeś możesz wyslać ją ponownie.
      </div>
      <div className="confirmEmailButton">
        <button className="btn btn-green btn-capitalize btn-radius btn-login">
          Wyślij ponownie
        </button>
      </div>
    </div>
  );
};

export default ConfirmEmail;
